 /* login form styles */

.form-side{
    width: 50%;
    box-sizing: border-box;
    text-align: center;
}


.form-side input{
    display: block;
    padding: 20px;
    width: 70%;
    margin: auto;
    border-radius: 40px;
    border: none;
    /* font-size: .8em; */
    outline: none;
    overflow: hidden;
}
input[type="password"] {
    font: small-caption;
    font-size: 16px;
    padding: 18px 20px;
}

.form-side input[type]::-webkit-input-placeholder{
    font: inherit;
    text-transform: capitalize;
    letter-spacing: 2px;
}

.form-side input[type]::-ms-input-placeholder{
    font: inherit;
    text-transform: capitalize;
    letter-spacing: 2px;
}

.form-side input[type]::placeholder{
    font: inherit;
    text-transform: capitalize;
    letter-spacing: 2px;
}

.form-side input{
    border: 1px solid rgb(170, 170, 206);
    margin: 20px auto;
    -webkit-transition: border .3s;
    transition: border .3s;
}

.form-side input:focus{
    border: 2px solid rgb(129, 129, 180);
}

.form-side sub{
    color: rgb(170, 170, 206);
    font-weight: bold;
    display: block;
    margin: 40px 0px;
    text-transform: capitalize;
    position: relative;
    left: -6%;
}

.form-side input[type="submit"]{
    font: inherit;
    color: rgb(228, 222, 222);
    background: rgb(36, 36, 214);
    padding: 12px 10px;
    width: 25%;
    position: relative;
    left: 22%;
    -webkit-transition: .3s background;
    transition: .3s background; 
    -o-box-shadow: -2px 2px 10px rgb(129, 127, 127); 
    box-shadow: -2px 2px 10px rgb(129, 127, 127); 
}

.form-side input[type="submit"]:hover{
    background: rgb(53, 53, 212);
    cursor: pointer;
}

.form-side input[type="submit"]:focus{
    border: none;
}

.recovery-and-creation{
    color: rgb(129, 129, 180);
    font-size: .8em;
    font-weight: bold;
    position: relative;
    top: 80px;
    width: 70%;
    margin: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}


.password-input{
    position: relative;
    width: 100%;
}

.password-input span{
    position: absolute;
    top: 35%;
    right: 20%;
    font-size: 1em;
    color: rgb(129, 129, 180);
    overflow: hidden;
}

.password-input span i:hover{
    cursor: pointer;
}

.error-message{
    color: white;
    background: rgb(209, 86, 86);
    padding: 10px;
    position: absolute;
    opacity: 0;
    border-radius: 10px;
    font-weight: bold;
    top: -140px;
    left: 35%;
    z-index: 5;
    font-size: .8em;
}

.valid{
    border-color: rgb(75, 114, 75) !important;
}

.invalid{
    border-color: rgb(182, 16, 16) !important;
}

.slide{
    -webkit-animation: slidedown 2s backwards;
            animation: slidedown 2s backwards;

}

@-webkit-keyframes slidedown{
    0%{
        opacity: 0;
        top: -140px;
    }
    70%{
        opacity: 1;
        top: -40px;
    }
    100%{
        opacity: 0;
        top: -140;
    }
}

@keyframes slidedown{
    0%{
        opacity: 0;
        top: -140px;
    }
    70%{
        opacity: 1;
        top: -40px;
    }
    100%{
        opacity: 0;
        top: -140;
    }
}
